import React, { Component } from "react";
import { Link } from 'react-router-dom';
import moment from 'moment'
import {Line} from 'react-chartjs-2';

import happy from '../assets/images/happy.png'

class EventAnalytics extends Component {


    render() {

        var events = JSON.parse(localStorage.getItem("allEvents"));
        events = events.sort((a,b) => moment(a.date + ' ' + a.time).diff(b.date + ' ' + b.time));                   
        
        var valenceValues = [];
        var arousalValues = [];
        var labels = [];
        events.map(e => {
            valenceValues.push(e.valence);
            arousalValues.push(e.arousal);
            labels.push(moment(e.date).format('dd/MM') + ' - ' + e.time)
        });

        var valenceDataSet = {
            data: valenceValues,
            label: 'Valence'
        };
        var arousalDataSet = {
            data: arousalValues,
            label: 'Arousal'
        };

        const data = {
            labels: labels,
            datasets: [arousalDataSet, valenceDataSet]
        };
        console.log('data', data);
        return (
            <div>
                <main role="main" className="container">
                    <div className="body my-3 p-3 text-center bg-light rounded shadow-sm">
                        <h2 className="sticky-top bg-light pb-2 mb-0">
                            Affective State Profile App
                        </h2>
                        <Line data={data} />
                        <br />
                    </div>
                </main>
            </div>
        );
    }
}

export default EventAnalytics;



